import { Toast } from "mint-ui";
export default {
  methods: {
    gotoClass(type) {
      this.$router.push({
        name: "goodslist",
        params: {
          type
        }
      });
    },
    gotoNull() {
      Toast({
        message: "正在进货，过几天再来吧",
        iconClass: "icon icon-tishi iconfont"
      });
    }
  }
};



