tips {\n  margin-top: 0.3em;\n  color: #999999;\n  padding-left: 0.15rem;\n  padding-right: 0.15rem;\n  font-size: 0.14rem;\n}\n\n.weui-cell {\n  padding: 0.1rem 0.15rem;\n  position: relative;\n  display: flex;\n  align-items: center;\n}\n\n.weui-cell:before {\n  content: \" \";\n  position: absolute;\n  left: 0;\n  top: 0;\n  right: 0;\n  height: 0.01rem;\n  border-top: 0.01rem solid #D9D9D9;\n  color: #D9D9D9;\n  transform-origin: 0 0;\n  transform: scaleY(0.5);\n  left: 0.15rem;\n}\n\n.weui-cell:first-child:before {\n  display: none;\n}\n\n.weui-cell_primary {\n  align-items: flex-start;\n}\n\n.weui-cell__bd {\n  flex: 1;\n}\n\n.weui-cell__ft {\n  text-align: right;\n  color: #999999;\n}\n\n.vux-cell-justify {\n  height: 4.11764706em;\n}\n\n.vux-cell-justify.vux-cell-justify:after {\n  content: \".\";\n  display: inline-block;\n  width: 100%;\n  overflow: hidden;\n  height: 0;\n}\n\n.weui-loading {\n  width: 0.2rem;\n  height: 0.2rem;\n  display: inline-block;\n  vertical-align: middle;\n  animation: weuiLoading 1s steps(12, end) infinite;\n  background: transparent url(\"data:image/svg+xml;base64,\") no-repeat;\n  background-size: 100%;\n}\n\n.weui-loading.weui-loading_transparent {\n  background-image: url(\"data:image/svg+xml;base64,\");\n}\n\n@-webkit-keyframes weuiLoading {\n  0% {\n    transform: rotate3d(0, 0, 1, 0deg);\n  }\n\n  100% {\n    transform: rotate3d(0, 0, 1, 360deg);\n  }\n}\n\n@keyframes weuiLoading {\n  0% {\n    transform: rotate3d(0, 0, 1, 0deg);\n  }\n\n  100% {\n    transform: rotate3d(0, 0, 1, 360deg);\n  }\n}\n\n.vux-cell-primary {\n  flex: 1;\n}\n\n.vux-label {\n  display: inline-block;\n  word-wrap: break-word;\n  word-break: break-all;\n}\n\n.weui-cell__ft .weui-loading {\n  display: inline-block;\n}\n\n.weui-cell__ft.vux-cell-align-left {\n  text-align: left;\n}\n\n.weui-cell.vux-cell-no-border-intent:before {\n  left: 0;\n}\n\n.weui-cell_access .weui-cell__ft.vux-cell-arrow-down:after {\n  transform: matrix(0.71, 0.71, -0.71, 0.71