import Vue from "vue";
import Vuex from "vuex";
// import { log } from "util";
Vue.use(Vuex);
const store = new Vuex.Store({

    state: {
        // 4
        shopinglist: [],
        searchlist: [],
        orderlist:{
        },
    },
    getters: {

    },
    // 2
    actions: {
        getshopping({ commit }, { url, tel }) {
            // console.log(url + "" + tel);
            Vue.http.get(url, {
                params: {
                    tel
                }
            }).then(res => {
                commit("getshopping", res.data.result)
            })
        },
        getsearch({ commit }, searchlist) {
            commit("getsearch", searchlist)
        }
    },
    // 3
    mutations: {
        getshopping(state, result) {
            for(var j = 0,len = result.length; j < len; j++){
                result[j].checked = false;
            }
            state.shopinglist = result;
        },
        getsearch(state, searchlist) {
            state.searchlist = searchlist;
        }
    }
})


export default store;


