import { Indicator, Toast } from "mint-ui";
export default {
  data() {
    return {
      types: [],
      allGoods: [],
      selected: "",
      show: false
    };
  },
  methods: {
    gotoInfo(id) {
      this.$router.push({
        name: "goodsinfo",
        params: {
          id
        }
      });
    }
  },
  created() {
    Indicator.open({
      text: "加载中...",
      spinnerType: "fading-circle"
    });
    this.$http.get("http://60.205.185.130:1810/vue/allgoods").then(res => {
      this.$nextTick(() => {
        this.allGoods = res.data.result;
        this.show = true;
        console.log(this.allGoods);
        Indicator.close();
        Toast({
          message: "加载成功",
          iconClass: 'icon icon-liwu iconfont',
          duration: 500
        });
      });
    });
  }
};



