export default {
  methods: {
    gotoShopcar() {
      if (localStorage.username) {
        this.$router.push({ name: "shoppingcar" });
      } else {
        this.$router.push({ name: "login" });
      }
    },
    gotoSearch() {
      this.$router.push({ name: "search" });
    }
  },
  computed: {
    shopinglist() {
      var num = 0;
      for (var i = 0; i < this.$store.state.shopinglist.length; i++) {
        num += this.$store.state.shopinglist[i].goodsnum * 1;
      }
      return num;
    }
  },
  created() {
    const tel = localStorage.username;
    // console.log(tel);
    // 1
    this.$store.dispatch("getshopping", {
      url: "http://60.205.185.130:1810/vue/shoppingcar",
      tel: tel
    });
  }
};



