import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
import { Indicator } from "mint-ui";
export default {
  data() {
    return {
      imgs: [],
      allGoods: []
    };
  },
  methods: {
    gotoShopcar() {
      if (localStorage.username) {
        this.$http
          .post("http://60.205.185.130:1810/vue/addshopcar", {
            params: {
              tel: localStorage.username,
              goodsid: this.allGoods[0].id,
              goodsname: this.allGoods[0].goodsname,
              goodsprice: this.allGoods[0].price,
              goodsimg: this.allGoods[0].imgSrc
            }
          })
          .then(res => {
            console.log(res);
            if (res.data.code == 200) {
              Toast({
                message: res.data.text,
                iconClass: "icon icon-chenggong iconfont"
              });
              Indicator.close();
            } else {
              Toast({
                message: res.data.text,
                iconClass: "icon icon-tishi iconfont"
              });
              Indicator.close();
            }
          });
        MessageBox.confirm("已添加,是否跳转购物车").then(action => {
          this.$router.push({ name: "shoppingcar" });
        });
      } else {
        Toast({
          message: "亲，还没有登录跳转登录~",
          iconClass: "icon icon-chenggong iconfont"
        });
        this.$router.push({ name: "login" });
      }
    },
    gotoOrder() {
      this.$router.push({ name: "myorder" });
    }
  },
  created() {
    this.$http.get("http://60.205.185.130:1810/vue/allgoods").then(res => {
      this.$nextTick(() => {
        // console.log(this.$route.params.type);
        // console.log(res.data.result);
        this.allGoods = res.data.result.filter(
          item => item.id == this.$route.params.id
        );
        console.log(this.allGoods);
      });
    });
  }
};



