import { Toast } from "mint-ui";
import { Indicator } from "mint-ui";
export default {
  data() {
    return {
      phone: "",
      password: ""
    };
  },
  methods: {
    log() {
      if (this.phone == "" || this.password == "") {
        Toast({
          message: "有必填项为空，请输入",
          iconClass: "icon icon-tishi iconfont"
        });
      } else {
        Indicator.open({
          text: "正在登录...",
          spinnerType: "fading-circle"
        });
        this.$http
          .post("http://60.205.185.130:1810/vue/log", {
            params: {
              tel: this.phone,
              pwd: this.password
            }
          })
          .then(res => {
            var { tel, pwd } = res.data;
            if (res.data.code == 200) {
              Toast({
                message: "登录成功",
                iconClass: "icon icon-chenggong iconfont"
              });
              Indicator.close();
              localStorage.username = this.phone;
              this.$router.go(-1);
            } else {
              Toast({
                message: res.data.text,
                iconClass: "icon icon-tishi iconfont"
              });
              Indicator.close();
            }
          });
      }
    }
  }
};



