import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
import { Indicator } from "mint-ui";
import { constants } from "crypto";
export default {
  methods: {
    gotoOrder() {
      let result = this.$store.state.shopinglist;
      let orderlist = [];
      for (var j = 0, len = result.length; j < len; j++) {
        if (result[j].checked) {
          orderlist.push(result[j]);
        }
      }
      this.$store.state.orderlist.order = orderlist;
      this.$store.state.orderlist.allnum = this.allnum;
      this.$store.state.orderlist.total = this.total;
      if (orderlist.length > 0) {
        this.$router.push({ name: "myorder" });
      } else {
        Toast({
          message: "请至少选中一个商品",
          iconClass: "icon icon-tishi iconfont"
        });
      }
    },
    addcar(item) {
      this.$http
        .post("http://60.205.185.130:1810/vue/addone", {
          params: {
            goodsid: item.goodsid,
            goodsnum: item.goodsnum
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            item.goodsnum++;
            Toast({
              message: res.data.text,
              iconClass: "icon icon-chenggong iconfont"
            });

            Indicator.close();
          } else {
            Toast({
              message: res.data.text,
              iconClass: "icon icon-tishi iconfont"
            });
            Indicator.close();
          }
        });
    },
    minuscar(item) {
      this.$http
        .post("http://60.205.185.130:1810/vue/minusone", {
          params: {
            goodsid: item.goodsid,
            goodsnum: item.goodsnum
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            if (item.goodsnum > 1) {
              item.goodsnum--;
              Toast({
                message: res.data.text,
                iconClass: "icon icon-chenggong iconfont"
              });
            } else {
              Toast({
                message: res.data.text,
                iconClass: "icon icon-tishi iconfont"
              });
            }
            Indicator.close();
          } else {
            Toast({
              message: res.data.text,
              iconClass: "icon icon-tishi iconfont"
            });
            Indicator.close();
          }
        });
    }
  },
  computed: {
    shopinglist() {
      // 5页面接收
      return this.$store.state.shopinglist;
    },
    quan: {
      get() {
        var all = true;
        this.shopinglist.forEach(item => {
          if (!item.checked) {
            all = false;
          }
        });
        return all;
      },
      set(newVal) {
        this.shopinglist = this.shopinglist.map(item => {
          item.checked = newVal;
          return item;
        });
      }
    },
    total: {
      get() {
        var total = 0;
        this.shopinglist.forEach(item => {
          if (item.checked) {
            total += item.goodsprice * item.goodsnum;
          }
        });
        return total;
      }
    },
    allnum: {
      get() {
        var allnum = 0;
        this.shopinglist.forEach(item => {
          if (item.checked) {
            allnum += item.goodsnum;
          }
        });
        return allnum;
      }
    }
  }
};



