import { Indicator, Toast } from "mint-ui";
export default {
  data() {
    return {
      types: [],
      allGoods: [],
      selected: "",
      show: false
    };
  },
  methods: {
    gotoInfo(id) {
      this.$router.push({
        name: "goodsinfo",
        params: {
          id
        }
      });
    }
  },
  computed:{
   searchlist(){
     return this.$store.state.searchlist;
   }
  },
};



