import { Toast } from "mint-ui";
import { Indicator } from "mint-ui";
export default {
  data() {
    return {
      phone: "",
      userphone: "",
      password: "",
      userpassword: "",
      passwordT: "",
      userpasswordT: ""
    };
  },
  methods: {
    getphone() {
      const phone = this.phone;
      if (phone.match(/^$/)) {
        this.userphone = "error";
      } else if (phone.match(/^1[34578][0-9]{9}/)) {
        this.userphone = "success";
      } else {
        this.userphone = "error";
      }
    },
    getpwd() {
      const password = this.password;
      if (password.match(/^$/)) {
        this.userpassword = "error";
      } else if (password.match(/^[a-z0-9]{6,10}$/gi)) {
        this.userpassword = "success";
      } else {
        this.userpassword = "error";
      }
    },
    getpwd2() {
      const passwordT = this.passwordT;
      if (passwordT.match(/^$/)) {
        this.userpasswordT = "error";
      } else if (passwordT.match(/^[a-z0-9]{6,10}$/gi)) {
        this.userpasswordT = "success";
      } else {
        this.userpasswordT = "error";
      }
    },

    reg() {
      if (this.phone == "" || this.password == "" || this.passwordT == "") {
        Toast({
          message: "有必填项为空，请输入",
          iconClass: "icon icon-tishi iconfont"
        });
      } else if (
        this.userphone == "error" ||
        this.userpassword == "error" ||
        this.userpasswordT == "error"
      ) {
        Toast({
          message: "输入不符合描述，请重新输入",
          iconClass: "icon icon-tishi iconfont"
        });
      } else if (this.passwordT != this.password) {
        Toast({
          message: "两次输入密码不一致，请重新输入",
          iconClass: "icon icon-tishi iconfont"
        });
      } else {
        Indicator.open({
          text: "正在注册...",
          spinnerType: "fading-circle"
        });
        this.$http
          .post("http://60.205.185.130:1810/vue/reg", {
            params: {
              tel: this.phone,
              pwd: this.password
            }
          })
          .then(res => {
            console.log(res);

            if (res.data.code == 200) {
              Toast({
                message: "注册成功,跳转登录",
                iconClass: "icon icon-chenggong iconfont"
              });
              this.$router.push({ name: "login" });

              Indicator.close();
            } else {
              Toast({
                message: "注册失败",
                iconClass: "icon icon-tishi iconfont"
              });
              Indicator.close();
            }
          });
      }
    }
  },
  created() {
    if (localStorage.username) {
      // this.$router.push({ name: "sale" });
      this.$router.go(-2);
    }
  }
};



