import { Search } from "mint-ui";
// Vue.component(Search.name, Search);
export default {
  name: "page-search",
  data() {
    return {
      value: "",
      defaultResult: []
    };
  },
  watch: {
    value: function(newVal) {
      this.$http
        .post("http://60.205.185.130:1810/vue/search", {
          params: {
            newVal: newVal
          }
        })
        .then(res => {
          let arr = res.body;
          let newArr = arr.map(val => {
            return val.goodsname;
          });
          this.defaultResult = newArr; //这个是页面循环的值
        });
    }
  },
  computed: {
    filterResult() {
      return this.defaultResult.filter(value =>
        new RegExp(this.value, "i").test(value)
      );
    }
  },
  methods: {
    switchCustomer(item) {
      this.$http
        .post("http://60.205.185.130:1810/vue/search", {
          params: {
            newVal: item
          }
        })
        .then(res => {
          this.$router.push({
            name: "searchlist"
          });
          this.$store.dispatch("getsearch", res.body);
        });
    },
    search(value) {
      this.$http
        .post("http://60.205.185.130:1810/vue/search", {
          params: {
            newVal: value
          }
        })
        .then(res => {
          this.$router.push({
            name: "searchlist"
          });
          this.$store.dispatch("getsearch", res.body);
        });
    }
  }
};



